  "people": ["Einzeln", "Zu zweit", "Gruppe", "Familie"],
  "title": "Kultur in Baden-Baden",
  "intro": "Ein Besuch im Museum Frieder Burda",
  "description": "Das Museum Frieder Burda als Lebenswerk\nUm die Sammlung zu bewahren und der Öffentlichkeit zugänglich zu machen, wurde 1998 –nachdem Bemühungen, diese im südfranzösischen Mougins zu etablieren, aufgegeben wurden – die Stiftung Frieder Burda gegründet. Sie legte den Grundstock für den vom amerikanischen Star-Architekten Richard Meier entworfenen Museumsbau, der seit 2004 die Sammlung Frieder Burda in Baden-Baden im Zusammenspiel mit anderen internationalen Meisterwerken präsentiert: Ein strahlend weißer Solitär in der historischen Lichtentaler Allee, heute vielmals als das „Juwel im Park“ bezeichnet.\n\nZahlreiche hochkarätige Ausstellungen wurden seitdem dort gezeigt, u.a. mit Gerhard Richter, Sigmar Polke, Katharina Grosse, William N. Copley, Andreas Gursky und James Turrell. Dazu kamen thematische Ausstellungen wie „Die Bilder tun was mit mir“ oder „America! America!“. Die engagierte und profunde Vermittlung von Kunst mit sozialem Anspruch durch die Kunstwerkstatt des Museums war Frieder Burda, Ehrenbürger der Stadt Baden-Baden, dabei immer eine Herzensangelegenheit. 2014 wurde mit zwei großen Übersichtsausstellungen unter dem Titel „40I10“ das 10jährige Jubiläum im Museum gefeiert. Aus Anlass des 15ten Jubiläums war 2019 aktuell die Ausstellung „Ensemble“ zu sehen, die anhand des Zusammenspiels mit Meisterwerken aus der Sammlung des Centre Pompidou in Paris die deutsch-französische Freundschaft feiert. Anfang 2019 hatte das Haus noch durch eine Banksy-Ausstellung für Furore gesorgt. Mit dem Salon Berlin, den Frieder Burdas Patricia Kamp leitet, schlägt das Museum eine Brücke in die Gegenwartskunst.\n\nImmer hat Frieder Burda sich neuen Anregungen und der Auseinandersetzung mit diesen gestellt. Zugleich blieb er sich stets treu in seinem Glauben an die verbindenden Kräfte der Kunst. Zu der Ausstellung „Ensemble“ hatte er, der leidenschaftliche Frankophile, noch im Katalog geschrieben: „Die Spiegelung der beiden Sammlungen, die Gegenüberstellung einzelner Werke deutscher und französischer Künstler zeigt, wie sehr die Kunst verbindet, sich über Grenzen hinweg positiv beeinflusst und ergänzt. Das macht mich froh und bestärkt mich im Glauben an das universell Verbindende der Kunst, ihre Gültigkeit und ihr Bestehenbleiben über alle Krisen hinweg.“ Am 14. Juli 2019, dem französischen Nationalfeiertag, ist Frieder Burda 83jährig in Baden-Baden im Kreis seiner Familie gestorben.\n\nUngebrochen war sein Glaube an die versöhnliche Macht der Kunst, uneingeschränkt seine Faszination für die mitreißende Kraft der Farben: Mit Frieder Burda hat die Kunstwelt einen seiner großen Sammler, der seine Liebe und Begeisterung für die Kunst immer mit möglichst vielen Menschen teilen wollte, verloren. Humanistische Werte waren ihm stets wichtiger als materielle, der Besitz von Kunst war ihm immer Verpflichtung, diese zugänglich zu machen.\n\nAls Freund der Künstler suchte Frieder Burda den Austausch über die Ursprünge der Kunst, den unmittelbaren und inspirierenden Kontakt mit den Schaffenden. Als Stifter und Museumsgründer beschenkte er seine Heimatstadt großzügig mit einem Haus, das bis heute Menschen aus aller Welt nach Baden-Baden lockt. Als Vorstand seiner Stiftung war er für seine Mitarbeiter immer Vorbild an Bescheidenheit und Menschlichkeit. Und als Privatmensch Seite an Seite mit seiner Frau Elke hat er es vermocht, Familie und Freunde im Zeichen der Kunst immer wieder neu zusammenzuführen.",
